.ant-layout{
  background: #fff;
}
.m-pc-head{
  display: flex;
  flex:1
}
@media screen and (min-width: 900px) {
  /* 大屏幕 */
  .m-pc-head{
    display: flex
  }
  .trigger,.m-sider{
     display: none
  }
}
@media screen and (max-width: 900px) {
  /* 小屏幕 */
  .m-pc-head{
    display: none
  }
  .trigger{
    display: inline-block;
  }
  .m-sider{
     display: block
  }
}